.expense-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    /* padding: 0.1rem; */
    margin: 1rem;
    border-radius: 12px;
    /* background-color: #4b4b4b; */
    background-color: rgba(25, 25, 25, 0.85);
    /* height: 100px; */

}
  
  .expense-item__description {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: flex-end;
    flex-flow: column-reverse;
    justify-content: flex-start;
    flex: 1;
  }
  
  .expense-item h2 {
    color: #3a3a3a;
    font-size: 1rem;
    flex: 1;
    margin: 0 1rem;
    color: white;
  }
  
  .expense-item__price {
    font-size: 1rem;
    font-weight: bold;
    color: white;
    /* background-color: #40005d; */
    /* background-color: rgba(61, 12, 61); */
    background-color: #514A9D;
    border: 1px solid white;
    padding: 0.5rem;
    border-radius: 12px;
  }

