.outer-form-div {
  display: flex;
  align-items: center;
  justify-content: center;
}

 .myForm {
  margin-top: 5%;
  border-radius: 12px;
  /* border: 1px solid purple; */
  border: 1px solid white;
  background-color: rgba(0, 0, 0, 0.2);
  /* padding: 5%; */
  width: 100%;
  /* box-shadow: 5px 8px 6px 8px rgba(61, 12, 61, 0.326); */
  box-shadow: 5px 8px 6px 8px #514a9d9f

}
table{
    padding: 20px;
}

label{
    font-weight: 500;
    font-size: 20px;
    /* padding: 30px; */
    /* color: rgba(61, 12, 61); */
    color: white;
}

input{
    margin-left: 100px;
    margin-right: 10px;
    /* padding-left: 100px; */
    /* margin-right: 30px; */
    font-size: 20px;
    width: 70%;
}

.button-div{
    text-align: center;
}

.button-style{
    width: 50%;
    height: 50px;
    border-radius: 10px;
    /* border-color: rgba(61, 12, 61); */
    border-color: white;
    padding: 10px;
    font-size: 20px;
    color: white;
    /* background-color: rgba(61, 12, 61); */
    background-color: #514A9D;
}

.button-style:hover{
    width: 50%;
    height: 50px;
    border-radius: 10px;
    border-color: rgba(61, 12, 61);
    padding: 10px;
    font-size: 20px;
    color: white;
    background-color: purple;
}
