.blink-cover{
    /* text-align: center; */
    display: flex;
    align-items: center;
    justify-content: center;
}
.blink2{
    width:70%;
    height: 100px;
    /* background-color: rgb(71, 43, 91); */
    /* background-color: rgba(61, 12, 61); */

    background: #24C6DC;  /* fallback for old browsers */
background: -webkit-linear-gradient(to bottom, #514A9D, #24C6DC);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to bottom, #514A9D, #24C6DC); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

box-shadow: 0 1px 8px white;

    
    /* padding: 15px;	 */
    margin-top: 1%;
    /* margin-left: 20px; */
    /* margin-right: 20px; */
    /* margin: 20px; */
    border-radius: 20px;
    /* position: relative; */



    text-align: center;
    line-height: 50px;
}
span{
    font-size: 25px;
    /* font-family: cursive; */
    color: white;
    animation: blink 1s linear infinite;
}
@keyframes blink{
0%{opacity: 0;}
50%{opacity: .5;}
100%{opacity: 1;}
}